.booking {
  position: relative;
  padding: 50px 0;
  background-color: #5586c5;

  @media (max-width: 1023px) {
    padding: 50px 0 100px;
  }

  @media (max-width: 767px) {
    padding: 20px 0 65px;
  }
}

.booking .container {
  display: flex;
  justify-content: space-between;
  position: inherit;
  z-index: 2;

  @media (max-width: 1023px) {
    flex-direction: column;
    align-items: center;
  }
}

.booking__description {
  margin: 163px 10px 0 60px;

  @media (max-width: 1023px) {
    margin: 38px 0 52px 0;
    text-align: center;
  }

  @media (max-width: 767px) {
    margin: 38px 0 33px;
  }
}

.booking__background-image {
  position: absolute;
  width: 100%;
  top: 0;
  right: 0;
  height: 100%;
  object-fit: cover;

  @media (max-width: 1023px) {
    width: inherit;
  }
}

.booking__description h2 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 150%;
  color: #011c40;
  margin: 0;
  margin-bottom: 8px;

  @media (max-width: 1023px) {
    font-size: 34px;
    margin-bottom: 5px;
  }

  @media (max-width: 767px) {
    line-height: 130%;
    font-size: 24px;
    margin-bottom: 14px;
  }
}

.booking__description p {
  margin: 0;
  width: 60%;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;
  color: #011c40;

  @media (max-width: 1300px) {
    width: 90%;
  }

  @media (max-width: 1023px) {
    margin: 0 auto;
    width: 59%;
  }

  @media (max-width: 767px) {
    font-size: 16px;
    line-height: 140%;
    width: 91%;
  }
}

.booking form {
  background: #011c40;
  box-shadow: 0 4px 45px rgba(1, 28, 64, 0.15);
  padding: 44px 50px 50px 50px;
  width: 36.3%;
  min-width: 380px;
  margin-right: 5%;
  height: max-content;

  @media (max-width: 1023px) {
    width: 59.6%;
    min-width: inherit;
    margin-right: 0;
  }

  @media (max-width: 767px) {
    padding: 35px 29px 40px;
    width: 100%;
  }

  & fieldset {
    border: none;
    margin: 0;
    padding: 0;
    margin-bottom: 20px;
  }
}

.booking__form-fieldset input {
  height: 43px;
  width: 100%;
  padding-left: 16px;
  border: none;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  color: rgba(1, 28, 64, 0.7);

  &:hover {
    box-shadow: 0 0 10px 5px rgba(221, 221, 221, 1);
    transition: 0.1s;
  }
}

.booking__form p {
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  color: #f9fbfd;
  margin-bottom: 27px;

  @media (max-width: 767px) {
    text-align: center;
    line-height: 140%;
  }
}

.booking__form-checkbox {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 30px;

  @media (max-width: 767px) {
    margin-bottom: 26px;
  }
}

.booking__form-checkbox input {
  appearance: none;
  position: relative;
  width: 18px;
  height: 18px;
  cursor: pointer;
  background-color: #f9fbfd;
  margin-right: 5px;

  &:hover {
    box-shadow: 0 0 10px 5px rgba(221, 221, 221, 1);
    transition: 0.1s;
  }

  @media (max-width: 767px) {
    margin-right: 9px;
    width: 27px;
    height: 19px;
  }
}

.booking__form-checkbox-label:hover {
 text-decoration: underline;
}

.booking__form-checkbox-input:checked::before {
  content: "";
  background-image: url("../img/svg/checkbox-icon.svg");
  background-repeat: no-repeat;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 12px;
  height: 8px;
}

.booking__form-checkbox label {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 130%;
  color: #f9fbfd;
  cursor: pointer;
}

.booking__form button {
  width: 100%;
  padding: 11px 0;
  border: none;
  background: #f9fbfd;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  color: #011c40;

  &:hover {
    box-shadow: 0 0 10px 5px rgba(221, 221, 221, 1);
    transition: 0.1s;
  }
}

.booking__form fieldset:last-of-type {
  @media (max-width: 767px) {
    margin-bottom: 18px;
  }
}
